import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
export default function Login() {
  const [credential,setCredential]=useState({email:"",password:""});
 let navigate = useNavigate();
    const handelSubmit = async (e)=>{
        e.preventDefault();
        const response = await fetch("https://difficult-gold-bison.cyclic.app/api/auth/login",{
            method: 'POST',
            headers: {
              'content-type': 'application/json'
            },
            body: JSON.stringify({email: credential.email, password: credential.password})
          }); 
          const json = await response.json();
          if(json.success){
            localStorage.setItem('token',json.authtoken);
            navigate('/');
          }
          else{
            alert('invalid');
          }
           }
           const onChange=(e)=>{
            setCredential({...credential,[e.target.name]:e.target.value})
           }
  return (
    <div>
     <form onSubmit={handelSubmit}>
     <h2 className='container'>Login to continue</h2>
  <div className="form-outline mb-4">
    <input type="email" id="email" style={{width:"10cm"}} value={credential.email} className="form-control" name="email" onChange={onChange} />
    <label className="form-label" htmlFor="email">Email address</label>
  </div>
  <div className="form-outline mb-4">
    <input type="password" style={{width:"10cm"}} id="password" value={credential.password} className="form-control" name="password" onChange={onChange} />
    <label className="form-label" htmlFor="password">Password</label>
  </div>

  <button type="submit" className="btn btn-primary btn-block mb-4" style={{width:"10cm"}} value="submit" name="submit">Sign in</button>
</form>
    </div>
  )
}
